.login_logo {
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
}

.login {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    background-color: white;
}

.login_container {
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}

.login_container h1 {
    font-weight: 500;
    margin-bottom: 20px;
}

.login_container form h5 {
    margin-bottom: 5px;
}

.login_container form input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}

.login_container p {
    margin-top: 15px;
    font-size: 12px;
    margin-bottom: 10px;
}

.login_signInButton {
    background-color: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
}

.login_registerButton {
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
}

/* Responsive styles for different devices */
@media screen and (max-width: 768px) {
    /* For tablets, iPhones, and small phones */
    .login_container {
        width: 80%;
        padding: 15px;
    }

    .login_signInButton,
    .login_registerButton {
        height: 25px;
    }
}