.checkoutProduct {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}

.checkoutProduct_info {
    padding-left: 20px;
}

.checkoutProduct_info button {
    background-color: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;

}

.checkoutProduct_image {
    object-fit: contain;
    width: 180px;
    height: 180px;
}

.checkoutProduct_rating {
    display: flex;
}

.checkoutProduct_title {
    font-size: 17px;
    font-weight: 800;
}

/* Responsive styles for different devices */
@media screen and (max-width: 768px) {
  /* For phones and small devices */
  .checkoutProduct {
      flex-direction: column;
      align-items: center;
  }

  .checkoutProduct_info {
      padding-left: 0;
      text-align: center;
  }

  .checkoutProduct_info button {
      width: 100%;
  }

  .checkoutProduct_image {
      width: 120px;
      height: 120px;
  }

  .checkoutProduct_title {
      font-size: 16px;
  }
}

@media screen and (min-width: 769px) and (max-width: 1024px) {
  /* For tablets and smaller laptops */
  .checkoutProduct_image {
      width: 150px;
      height: 150px;
  }
}